.quiz {
  padding: 50px 0 200px;
  .swiper {
    overflow: unset;
  }
  &-slide {
    display: flex;
    align-items: stretch;
    gap: 32px;
    padding-left: max(30px, calc((100% - 1200px) / 2));
  }
  .form-wrap {
    max-width: 637px;
    width: 100%;
    background: #F9F7F3;
    border-radius: 50px;
    padding: 32px 50px;
  }
  &-img {
    width: calc(100% - 669px);
    max-width: 950px;
    img {
      max-width: 100%;
      width: 100%;
      height: 100%;
      border-radius: 50px 0 0 50px;
    }
  }
  &-img2 {
    position: relative;
    img {
      width: auto;
      height: auto;
      border-radius: 0;
    }
  }
  .quiz-line {
    position: absolute;
    top: 30px;
    left: -60px;
    max-width: 835px;
    pointer-events: none;
    img {
      width: 100%;
    }
  }
  .quiz-line2 {
    position: absolute;
    top: 310px;
    left: 80px;
    max-width: 532px;
    pointer-events: none;
    img {
      width: 100%;
    }
  }
  .swiper-slide {
    opacity: 0 !important;
    pointer-events: none;
    .form-line {
      display: none;
    }
  }
  .swiper-slide-active {
    opacity: 1 !important;
    pointer-events: auto;
    .form-line {
      display: block;
    }
  }
  .swiper-button-prev,
  .swiper-button-next {
    position: static;
    z-index: 1;
    margin-top: 0;
    cursor: pointer;
    &:after {
      display: none;
    }
  }
  .swiper-button-disabled {
    opacity: 1;
    pointer-events: auto;
  }
}

@media screen and (max-width: 1400px) {
  .quiz {
    &-slide {
      gap: 20px;
    }
    .form-wrap {
      max-width: 500px;
      padding: 32px;
    }
    &-img {
      width: calc(100% - 520px);
    }
  }
}

@media screen and (max-width: 992px) {
  .quiz {
    padding: 40px 0;
    &-slide {
      flex-direction: column;
      padding: 0 15px;
    }
    .form-wrap {
      max-width: 100%;
    }
    &-img {
      width: 100%;
      img {
        border-radius: 50px;
      }
    }
    .quiz-line {
      left: -15px;
    }
    .quiz-line2 {
      top: unset;
      bottom: -10px;
    }
  }
}

@media screen and (max-width: 576px) {
  .quiz {
    .form-wrap {
      border-radius: 30px;
    }
    &-img {
      img {
        border-radius: 30px;
      }
    }
  }
}