.login {
  height: 100%;
  padding: 80px 0 360px;
  &__inner {
    position: relative;
    z-index: 2;
  }
  .form {
    &-wrap {
      max-width: 580px;
      background: #F9F7F3;
      border-radius: 50px;
      padding: 40px;
      margin: 0 auto;
    }
    &-title {
      text-align: center;
    }
  }
  &-img {
    position: absolute;
    right: 100px;
    bottom: 0;
    max-width: 1100px;
    pointer-events: none;
    img {
      max-width: 100%;
      max-height: 100%;
    }
  }
  &-img2 {
    position: absolute;
    top: 232px;
    right: 0;
    max-width: 531px;
    pointer-events: none;
    img {
      width: 100%;
    }
  }
  &-img3 {
    position: absolute;
    left: 0;
    bottom: 0;
    width: 100%;
    pointer-events: none;
    img {
      width: 100%;
    }
  }
}

@media screen and (max-width: 992px) {
  .login {
    position: relative;
    padding: 120px 0 75px;
    &-img {
      top: 0;
      left: -150px;
      right: unset;
      bottom: unset;
    }
    &-img2 {
      top: -12px;
    }
  }
}

@media screen and (max-width: 576px) {
  .login {
    .form {
      &-wrap {
        max-width: 300px;
        padding: 30px 15px;
      }
    }
    &-img3 {
      left: unset;
      right: -200px;
      width: 200%;
    }
  }
}

@media screen and (max-width: 400px) {
  .login {
    &-img {
      left: -50px;
    }
  }
}