.wrapper {
  position: relative;
  min-height: 100%;
  overflow: hidden;
}
.btn-green {
  background: #00A500;
  border-radius: 20px;
  color: #fff;
  &:hover {
    background: #00dc00;
  }
  span {
    line-height: 140%;
    font-weight: 500;
  }
}
.btn-green__outline {
  background: transparent;
  border: 1px solid #00A500;
  border-radius: 20px;
  &:hover {
    background: #00A500;
    span {
      color: #fff;
    }
  }
  span {
    line-height: 140%;
    font-weight: 500;
    color: #00A500;
  }
}
.text48 {
  font-size: 48px;
  line-height: 140%;
  font-weight: 700;
  text-transform: uppercase;
}
.form {
  display: grid;
  grid-template-columns: 100%;
  gap: 25px;
  &-wrap {
    position: relative;
  }
  &-head {
    margin-bottom: 35px;
  }
  &-text {
    font-size: 20px;
    line-height: 140%;
    font-weight: 500;
    margin-top: 10px;
  }
  &-group {
    input {
      width: 100%;
      height: 74px;
      background: #fff;
      border: none;
      border-radius: 20px;
      padding: 0 30px;
      font-size: 16px;
      line-height: 140%;
      font-weight: 500;
      color: #000;
    }
  }
  &-btn {
    width: 100%;
    height: 74px;
    padding: 16px;
    span {
      font-size: 20px;
    }
  }
  &-info {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
    gap: 8px;
  }
  .help-text,
  .help-link {
    font-size: 16px;
    line-height: 140%;
    font-weight: 500;
    &:hover {
      color: #00A500;
    }
  }
  &-line {
    position: absolute;
    left: 50%;
    bottom: -100px;
    transform: translate(-50%, 0);
    width: 835px;
    pointer-events: none;
    img {
      width: 100%;
    }
  }
  &-tabs__head {
    display: grid;
    grid-template-columns: repeat(2, calc(50% - 20px / 2));
    gap: 20px;
  }
  .tab {
    width: 100%;
    height: 74px;
    display: flex;
    justify-content: center;
    align-items: center;
    border: 1px solid #00A500;
    border-radius: 20px;
    padding: 5px;
    font-size: 20px;
    line-height: 140%;
    font-weight: 500;
    color: #00A500;
    cursor: pointer;
    transition: all 0.3s;
  }
  .tab--active {
    background: #00A500;
    color: #fff;
  }
  .tab__content {
    display: none;
  }
  .tab__content--active {
    display: block;
  }
  &-check {
    position: relative;
    display: flex;
    input {
      position: absolute;
      z-index: 2;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      padding: 0;
      margin: 0;
      cursor: pointer;
      opacity: 0;
    }
    input:checked ~ label {
      &:after {
        background-image: url("../img/check.svg");
      }
    }
    label {
      position: relative;
      font-size: 14px;
      line-height: 140%;
      font-weight: 500;
      padding: 5px 0 5px 50px;
      &:after {
        content: '';
        position: absolute;
        top: -1px;
        left: 0;
        width: 33px;
        height: 33px;
        background-repeat: no-repeat;
        background-position: center;
        background-size: auto;
        border: 1px solid #00A500;
        border-radius: 5px;
      }
      a {
        position: relative;
        z-index: 3;
        &:hover {
          color: #00A500;
        }
      }
    }
  }
  &-subtitle {
    display: block;
    font-size: 20px;
    line-height: 140%;
    font-weight: 500;
    margin-bottom: 15px;
    &2 {
      margin-bottom: 0 !important;
    }
  }
  .nice-select {
    width: 100%;
    height: 74px;
    display: flex;
    align-items: center;
    background: #fff;
    border: none;
    border-radius: 20px;
    padding: 0 50px 0 30px;
    font-size: 16px;
    line-height: 140%;
    font-weight: 500;
    color: #000;
    &:after {
      top: 50%;
      right: 20px;
      transform: translate(0, -50%) rotate(0);
      width: 18px;
      height: 10px;
      background-image: url("../img/check.svg");
      background-repeat: no-repeat;
      background-size: contain;
    }
    .list {
      li {
        font-size: 16px;
        padding: 15px 30px;
      }
    }
  }
  .open {
    &:after {
      transform: translate(0, -50%) rotate(180deg) !important;
    }
  }
  &-checkboxes {
    display: grid;
    grid-template-columns: 100%;
    gap: 20px;
    &2 {
      grid-template-columns: repeat(2, calc(50% - 20px / 2));
    }
  }
  &-checkbox {
    position: relative;
    display: flex;
    input {
      position: absolute;
      z-index: 2;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      padding: 0;
      margin: 0;
      cursor: pointer;
      opacity: 0;
    }
    input:checked ~ label {
      &:before {
        opacity: 1;
      }
    }
    label {
      position: relative;
      font-size: 16px;
      line-height: 140%;
      font-weight: 500;
      padding-left: 40px;
      &:after {
        content: '';
        position: absolute;
        top: -5px;
        left: 0;
        width: 28px;
        height: 28px;
        background: #fff;
        border: 1px solid #D9D9D9;
        border-radius: 50%;
      }
      &:before {
        content: '';
        position: absolute;
        z-index: 1;
        top: 3px;
        left: 8px;
        width: 12px;
        height: 12px;
        background: #00A500;
        border-radius: 50%;
        opacity: 0;
      }
    }
  }
}


@media screen and (max-width: 576px) {
  .text48 {
    font-size: 24px;
  }
  .form {
    gap: 15px;
    &-head {
      margin-bottom: 15px;
    }
    &-text {
      font-size: 14px;
      margin-top: 5px;
    }
    &-group {
      input {
        height: 50px;
        border-radius: 10px;
        padding: 0 15px;
        font-size: 14px;
      }
    }
    &-btn {
      height: 50px;
      border-radius: 10px;
      span {
        font-size: 14px;
      }
    }
    &-info {
      margin-top: 8px;
    }
    .help-text,
    .help-link {
      font-size: 12px;
    }
    &-line {
      bottom: -32px;
      width: 412px;
    }
    &-tabs__head {
      gap: 15px;
    }
    .tab {
      height: 40px;
      border-radius: 10px;
      font-size: 14px;
    }
    &-check {
      margin-top: 10px;
      label {
        min-height: unset;
        font-size: 11px;
        padding-left: 32px;
        &:after {
          top: 0;
          width: 22px;
          height: 22px;
          background-size: 16px 13px;
        }
      }
    }
    .nice-select {
      height: 50px;
      border-radius: 10px;
      padding: 0 35px 0 15px;
      font-size: 14px;
      &:after {
        right: 10px;
      }
      .list {
        li {
          font-size: 14px;
          padding: 15px;
        }
      }
    }
    &-subtitle {
      font-size: 14px;
      margin-bottom: 8px;
    }
    &-checkboxes {
      grid-template-columns: 100% !important;
      gap: 12px;
    }
    &-checkbox {
      label {
        font-size: 14px;
        padding-left: 20px;
        &:after {
          top: 2px;
          width: 14px;
          height: 14px;
        }
        &:before {
          top: 6px;
          left: 4px;
          width: 6px;
          height: 6px;
        }
      }
    }
  }
}