.header {
  padding: 24px 0;
  &__inner {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 24px;
  }
  &__logo {
    width: 178px;
    display: block;
    img {
      width: 100%;
    }
  }
  &__list {
    display: flex;
    gap: 120px;
    li a {
      font-size: 22px;
      line-height: 140%;
      font-weight: 500;
      &:hover {
        color: #00A500;
      }
    }
    .active {
      font-weight: 700;
      color: #00A500;
    }
  }
  &__group {
    display: flex;
    align-items: center;
    gap: 16px;
  }
  &__user {
    position: relative;
    display: flex;
    align-items: center;
    gap: 20px;
    padding-right: 40px;
    &-icon {
      width: 23px;
      flex-shrink: 0;
      svg {
        width: 100%;
      }
    }
    &-text {
      font-size: 22px;
      line-height: 140%;
      font-weight: 500;
    }
    &-icon2 {
      position: absolute;
      top: -14px;
      right: 0;
      width: 32px;
      img {
        width: 100%;
      }
    }
  }
}
.burger {
  position: relative;
  z-index: 15;
  width: 27px;
  height: 27px;
  flex-shrink: 0;
  display: none;
  background: #000;
  border-radius: 50%;
  cursor: pointer;
  transition: all 0.3s;
  span {
    position: absolute;
    left: 50%;
    transform: translate(-50%, 0);
    width: 14px;
    height: 2px;
    background: #fff;
    border-radius: 5px;
    transition: all 0.3s;
  }
  span:nth-child(1) {
    top: 7px;
  }
  span:nth-child(2) {
    top: 50%;
    transform: translate(-50%, -50%);
  }
  span:nth-child(3) {
    bottom: 7px;
  }
  &--active {
    background: #fff;
    span {
      background: #000;
    }
    span:nth-child(1) {
      top: 12px;
      transform: translate(-50%, 0) rotate(-45deg);
    }
    span:nth-child(2) {
      display: none;
    }
    span:nth-child(3) {
      bottom: 13px;
      transform: translate(-50%, 0) rotate(45deg);
    }
  }
}

@media screen and (max-width: 1200px) {
  .header {
    &__list {
      gap: 32px;
    }
  }
}

@media screen and (max-width: 768px) {
  .header {
    padding: 12px 0;
    &__list {
      position: fixed;
      z-index: 10;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      flex-direction: column;
      align-items: center;
      background: #000;
      padding: 100px 15px 50px;
      opacity: 0;
      pointer-events: none;
      transition: all 0.3s;
      &--active {
        opacity: 1;
        pointer-events: auto;
      }
      li a {
        color: #fff;
      }
    }
    &__user {
      padding-right: 8px;
      &-text {
        display: none;
      }
      &-icon2 {
        top: -8px;
        width: 18px;
      }
    }
  }
  .burger {
    display: block;
  }
}

@media screen and (max-width: 576px) {
  .header {
    &__logo {
      width: 112px;
    }
  }
}