.account {
  padding: 80px 0 160px;
  &__wrap {
    display: flex;
    justify-content: space-between;
    gap: 20px;
  }
  &__left {
    position: relative;
    z-index: 2;
    width: 480px;
  }
  &__right {
    width: calc(100% - 500px);
  }
  table {
    width: 100%;
    table-layout: fixed;
    border-collapse: collapse;
    thead {
      color: #fff;
      tr {
        th {
          text-align: start;
          background: #064BB4;
          padding: 18px 5px 18px 30px;
          &:last-child {
            border-radius: 0 50px 0 0;
            padding-right: 30px;
          }
        }
      }
    }
    tbody {
      position: relative;
      &:after {
        content: '';
        position: absolute;
        top: 23px;
        left: 50%;
        transform: translate(-50%, 0);
        width: 1px;
        height: 127px;
        background: #BCBCBC;
      }
      tr {
        &:first-child {
          td {
            padding-top: 40px;
          }
        }
        &:last-child {
          td {
            padding-bottom: 70px;
          }
        }
        &:not(:last-child) {
          td {
            padding-bottom: 8px;
          }
        }
        td {
          vertical-align: top;
          background: #F9F7F3;
          padding: 0 5px 0 30px;
          &:last-child {
            padding-right: 30px;
          }
        }
        .rowspan {
          padding-bottom: 120px !important;
        }
      }
    }
  }
  .table-group {
    display: flex;
    justify-content: space-between;
    gap: 8px;
  }
  .table-subtitle {
    font-size: 18px;
    line-height: 170%;
    font-weight: 700;
  }
  .table-text {
    font-size: 16px;
    line-height: 170%;
    font-weight: 300;
    margin-top: 8px;
  }
  .edit {
    display: flex;
    align-items: center;
    width: 19px;
    cursor: pointer;
    img {
      width: 100%;
    }
  }
  .delete {
    width: 18px;
    flex-shrink: 0;
    margin-top: 16px;
    cursor: pointer;
    img {
      width: 100%;
    }
  }
  &__btn {
    width: 100%;
    height: 67px;
    padding: 5px 16px;
    margin-top: 12px;
    span {
      font-size: 20px;
    }
  }
  &__group {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    gap: 8px 32px;
  }
  &__info {
    font-size: 16px;
    line-height: 140%;
    font-weight: 500;
  }
  &__slider {
    margin-top: 56px;
  }
  .swiper {
    overflow: unset;
  }
  .swiper-wrapper {
    align-items: stretch;
  }
  .swiper-slide {
    opacity: 0;
    pointer-events: none;
    transition: all 0.3s;
    &-active,
    &-active + .swiper-slide,
    &-active + .swiper-slide + .swiper-slide,
    &-active + .swiper-slide + .swiper-slide + .swiper-slide {
      opacity: 1;
      pointer-events: auto;
    }
  }
  .disabled {
    .account-card__group {
      position: relative;
      &:before {
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background: rgba(255, 255, 255, 0.6);
        backdrop-filter: blur(10px);
      }
      &:after {
        content: '';
        position: absolute;
        z-index: 2;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        width: 33px;
        height: 33px;
        background-image: url("../img/disabled-icon.svg");
        background-repeat: no-repeat;
        background-position: center;
        background-size: contain;
      }
    }
  }
  &__bottom {
    display: flex;
    justify-content: center;
    margin-top: 156px;
  }
  &__btn2 {
    height: 74px;
    padding: 5px 40px;
    span {
      font-size: 20px;
    }
  }
}
.account-card {
  height: auto;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  background: #F9F7F3;
  border-radius: 30px;
  padding: 12px;
  &__group {
    margin-top: -34px;
  }
  &__img {
    max-width: 177px;
    margin: 0 auto;
    img {
      width: 100%;
    }
  }
  &__title {
    font-size: 20px;
    line-height: 140%;
    font-weight: 700;
    margin-top: 14px;
  }
  &__text {
    margin-top: 12px;
    p {
      font-size: 18px;
      line-height: 170%;
      font-weight: 300;
    }
  }
  &__price {
    font-size: 18px;
    line-height: 170%;
    font-weight: 700;
    margin-bottom: auto;
  }
  &__btn {
    height: 66px;
    padding: 16px;
    margin-top: 40px;
    span {
      font-size: 16px;
    }
  }
}
.account2 {
  padding: 110px 0 420px;
  &__inner {
    position: relative;
    z-index: 1;
    display: flex;
    justify-content: space-between;
    align-items: stretch;
    gap: 20px;
  }
  &__left {
    width: 41%;
    background: #F9F7F3;
    border-radius: 50px;
    padding: 46px 55px 61px 55px;
  }
  &__right {
    width: 59%;
    display: flex;
    flex-direction: column;
    align-items: center;
    background-image: url("../img/account2-img.webp");
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    border-radius: 50px;
    padding: 64px 16px 32px;
  }
  &__name {
    font-size: 18px;
    line-height: 170%;
    font-weight: 700;
  }
  &__items {
    display: grid;
    grid-template-columns: 100%;
    gap: 20px;
    margin-top: 20px;
  }
  &__item {
    display: flex;
    gap: 8px;
    &:hover {
      color: #00A500;
    }
    i {
      width: 16px;
      flex-shrink: 0;
      img {
        max-width: 100%;
      }
    }
    span {
      font-size: 18px;
      line-height: 170%;
      font-weight: 700;
    }
  }
  &__btn {
    width: 100%;
    height: 74px;
    padding: 5px 16px;
    margin-top: 40px;
    span {
      font-size: 20px;
    }
  }
  &__exit {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 15px;
    margin-top: 40px;
    &:hover {
      color: #00A500;
      i svg path {
        fill: #00A500;
      }
    }
    i {
      width: 20px;
      flex-shrink: 0;
      svg {
        width: 100%;
      }
      svg path {
        transition: all 0.3s;
      }
    }
    span {
      font-size: 18px;
      line-height: 170%;
      font-weight: 700;
    }
  }
  &__text {
    position: relative;
    padding: 12px 12px 12px 32px;
    margin-left: 15px;
    margin-bottom: auto;
    &:after {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      width: calc(100% + 16px);
      height: 100%;
      background: #00A500;
      border-radius: 50px 0 0 50px;
    }
    p {
      position: relative;
      z-index: 2;
      font-size: 24px;
      line-height: 140%;
      font-weight: 700;
      color: #fff;
    }
  }
  .promocode {
    max-width: 420px;
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    background: #fff;
    border-radius: 50px;
    padding: 20px;
    margin-top: 36px;
    &-title {
      font-size: 18px;
      line-height: 140%;
      font-weight: 700;
    }
    &-text {
      width: 100%;
      background: #F2F2F2;
      border-radius: 50px;
      padding: 6px;
      font-size: 40px;
      line-height: 170%;
      font-weight: 700;
      margin-top: 8px;
    }
    &-copy {
      font-size: 16px;
      line-height: 140%;
      font-weight: 500;
      text-decoration: underline;
      margin-top: 8px;
      cursor: pointer;
    }
  }
  &__text2 {
    position: absolute;
    z-index: 1;
    left: 15px;
    bottom: 80px;
    max-width: 265px;
    width: 100%;
    font-size: 24px;
    line-height: 140%;
    font-weight: 500;
    color: #fff;
  }
  &__img {
    position: absolute;
    left: 0;
    bottom: 0;
    width: 100%;
    min-width: 1920px;
    pointer-events: none;
    img {
      width: 100%;
    }
  }
}


@media screen and (max-width: 992px) {
  .account {
    padding: 24px 0 100px;
    &__wrap {
      flex-direction: column;
    }
    &__left {
      width: 100%;
      max-width: 480px;
    }
    &__right {
      width: 100%;
    }
    &__bottom {
      margin-top: 40px;
    }
  }
  .account2 {
    padding: 20px 0 220px;
    &__left {
      width: 50%;
      border-radius: 30px;
      padding: 32px;
    }
    &__right {
      width: 50%;
      border-radius: 30px;
      padding: 32px 16px;
    }
    &__text {
      padding: 16px;
      margin-left: 0;
      p {
        font-size: 16px;
      }
    }
    .promocode {
      border-radius: 30px;
      padding: 10px 16px;
      &-text {
        font-size: 24px;
      }
    }
    &__text2 {
      display: none;
    }
    &__img {
      min-width: 1200px;
    }
  }
}

@media screen and (max-width: 768px) {
  .account {
    &__left {
      max-width: 100%;
    }
  }
}

@media screen and (max-width: 576px) {
  .account {
    table {
      thead {
        tr {
          th {
            padding: 12px 5px 12px 30px;
            &:last-child {
              padding-right: 18px;
            }
          }
        }
      }
      tbody {
        &:after {
          height: 77px;
        }
        tr {
          &:first-child {
            td {
              padding-top: 30px;
            }
          }
          &:last-child {
            td {
              padding-bottom: 70px;
            }
          }
          td {
            padding: 0 5px 0 30px;
            &:last-child {
              padding-right: 18px;
            }
          }
          .rowspan {
            padding-bottom: 70px !important;
          }
        }
      }
    }
    .table-subtitle {
      font-size: 11px;
    }
    .table-text {
      font-size: 10px;
    }
    .edit {
      width: 12px;
    }
    .delete {
      width: 12px;
    }
    &__btn {
      height: 40px;
      padding: 5px 16px;
      span {
        font-size: 12px;
      }
    }
    &__info {
      font-size: 14px;
    }
    &__btn2 {
      height: 55px;
      padding: 5px 30px;
      span {
        font-size: 15px;
      }
    }
    .disabled {
      .account-card__group {
        &:after {
          width: 22px;
          height: 22px;
        }
      }
    }
  }
  .account-card {
    &__img {
      max-width: 120px;
    }
    &__title {
      font-size: 14px;
      margin-top: 10px;
    }
    &__text {
      p {
        font-size: 12px;
      }
    }
    &__price {
      font-size: 12px;
    }
    &__btn {
      height: 40px;
      padding: 5px 16px;
      span {
        font-size: 12px;
      }
    }
  }
  .account2 {
    &__inner {
      flex-direction: column;
    }
    &__left {
      width: 100%;
    }
    &__right {
      width: 100%;
    }
    &__name {
      font-size: 14px;
    }
    &__items {
      gap: 16px;
      margin-top: 16px;
    }
    &__item {
      i {
        width: 12px;
      }
      span {
        font-size: 14px;
      }
    }
    &__btn {
      height: 56px;
      margin-top: 30px;
      span {
        font-size: 14px;
      }
    }
    &__exit {
      gap: 8px;
      margin-top: 30px;
      i {
        width: 12px;
      }
      span {
        font-size: 14px;
      }
    }
  }
}